import React from "react";
import './App.css';
import { motion } from 'framer-motion';
import { useState, useEffect, useRef } from 'react';
import Images from './Images';

import Button from './components/Button';
import ButtonSmall from './components/ButtonSmall';
import SearchBox from './components/SearchBox';
import Profile from './components/Profile';
import Bar from './components/Bar';
import CardRecomendation from './components/CardRecomendation';
import Tab from './components/Tab';
import CsButton from './components/CsButton';
import Navbar from './components/Navbar';
import Hiro from './components/Hiro';
import Filter from './components/Filter';

import Home from './pages/Home';
import Settings from './pages/Settings';
import Pengembangan from './pages/Pengembangan';
import Invest from './pages/Invest';

import profileImage from './IMG/img-2.jpg';

import {
  BrowserRouter as Router,
  Route,
  Routes,
  Link,
} from 'react-router-dom';

function App() {

  const [dataCard, setDataCard] = useState([]);

  useEffect(() => {
    const getData = async () => {
      const tasksFromServer = await fetchData();
      setDataCard(tasksFromServer);
    }
    getData();
  }, []);
  // Fetch Data
  const fetchData = async () => {
    const res = await fetch('http://localhost:5000/card');
    const data = await res.json();

    return data;
  }

  return (
    <div className="App"
      style={{
        backgroundColor: "#EFEDF4",
      }}>
      <div className="container"
        style={{
          maxWidth: 500,
          height: "100vh",
          backgroundColor: "#FFF",
          margin: "0 auto",
          position: "relative",
          overflowX: "hidden",
        }}>
        {/* <div className="padding"
          style={{
            padding: "0 24px",
          }}>

        </div> */}
        <Router>
          <Routes>
            <Route path="/" element={ <Home /> } />
            <Route path="/pengaturan" element={ <Settings /> } />
            <Route path="/portfolio" element={ <Pengembangan /> } />
            <Route path="/forum" element={ <Pengembangan /> } />
            {
              dataCard.map(data => {
                return <Route path={`/data/${data.id}`}
                element={ <Invest
                  title={data.title}
                  img={data.img}
                  target={data.target}
                  fund={data.fund}
                  description={data.description}
                  location={data.location}
                  investor={data.investor}
                  duration={data.duration}
                /> }
                key={data.id} />
              })
            }
          </Routes>
          <Navbar data={dataCard} />
        {/* {console.log("app", dataCard)} */}
        </Router>
      </div>
    </div>
  );
}

export default App;
