import Button from '../components/Button';
import SearchBox from '../components/SearchBox';
import Profile from '../components/Profile';
import CardRecomendation from '../components/CardRecomendation';
import Tab from '../components/Tab';
import CsButton from '../components/CsButton';
import Hiro from '../components/Hiro';
import Filter from '../components/Filter';
import CardSlider from '../components/CardSlider';

import { motion } from 'framer-motion';
import { useState, useEffect, useRef } from 'react';

const Home = () => {

  const [width, setWidth] = useState(0);
  const item = useRef();

  useEffect(() => {
    //20 -> margin left... 5 -> banyak tab
    setWidth( (item.current.offsetWidth - 20 * 6) - item.current.scrollWidth);
    // setWidthCard( (itemCard.current.offsetWidth - 20 * 33) - itemCard.current.scrollWidth);
  }, [])

  return (
    <div className="home-page" style={{ padding: "0 24px", }}>
      <header className="flex justify-between items-center"
        style={{
          padding: "24px 0",
        }}>
        <div className="text-content">
          <div className="hs medium" style={{ color: "#013237", }}>Hai, Divavor
          </div>
          <div className="md regular" style={{ color: "#A19CB9", }}>Mulai investasimu!
          </div>
        </div>
        <Profile />
      </header>
      <Hiro />
      <div className="search-section flex" style={{ margin: "32px 0 24px 0", }}>
        <SearchBox />
        <div className="search-wraper" style={{ marginLeft: 12, }}>
          <Filter />
        </div>
      </div>
      <motion.div drag="x" dragConstraints={{right: 0, left: width}} ref={item}
        className="tab-section flex" style={{ paddingBottom: 24, width: "160%"}}>

        <Tab text="Residence" />
        <Tab text="Cluster" type={false} />
        <Tab text="Townhouse" type={false} />
        <Tab text="Ruko" type={false} />
        <Tab text="Boarding House" type={false} />
      </motion.div>
      {/* <motion.div drag="x" dragConstraints={{right: 0, left: widthCard}} ref={itemCard}
        className="card-section flex" style={{ width: "300%" }}>
        <CardRecomendation />
        <CardRecomendation />
        <CardRecomendation />
        <CardRecomendation />
        <CardRecomendation />
      </motion.div> */}
      <CardSlider />
      <div className="lg semi-bold" style={{ margin: "24px 0", color: "#013237" }}>Layanan Bantuan
      </div>
      <CsButton />
      <CsButton cs={false} />
      <div className="footer" style={{
        marginBottom: 100,
      }}>
      </div>

    </div>
  )

}

export default Home;
