import { useState, useEffect, useRef } from 'react';
import { motion } from 'framer-motion';
import CardRecomendation from '../components/CardRecomendation';
import { useNavigate } from 'react-router-dom';


const CardSlider = () => {

  const [dataCard, setDataCard] = useState([]);
  const itemCard = useRef();
  const [widthCard, setWidthCard] = useState(0);

  const navigate = useNavigate();

  const directPath = (id) => {
    // console.log("Clicked");
    navigate(`/data/${id}`);
    // navigate(`/data`);
  }

  useEffect(() => {
    const getData = async () => {
      const tasksFromServer = await fetchData();
      setDataCard(tasksFromServer);
    }
    getData();
    setWidthCard( (itemCard.current.offsetWidth - 20 * 33) - itemCard.current.scrollWidth);
  }, []);

  //Fetch Tasks
  const fetchData = async () => {
    const res = await fetch('http://localhost:5000/card');
    const data = await res.json();

    return data;
  }

  // console.log(dataCard[0]);

  return (
    <div className="card-slider">
      <motion.div drag="x" dragConstraints={{right: 0, left: widthCard}} ref={itemCard}
        className="card-section flex" style={{ width: "300%" }}>
        {
          dataCard.map(data => {
            return <CardRecomendation
              title={data.title}
              img={data.img}
              target={data.target}
              fund={data.fund}
              key={data.id}
              directPath={directPath}
              currentID={data.id}
              data={data}
            />
          })
        }
      </motion.div>
    </div>
  )

}

export default CardSlider;
