import filterIcon from '../icons/filter.svg';

const Filter = () => {

  return (
    <div className="filter bg-gradient-green grid place-items-center"
      style={{
        width: 45,
        height: 45,
        borderRadius: 12,

      }}>
      <img src={filterIcon} alt=""/>
    </div>
  )

}

export default Filter;
