import illustration from '../assets/pengembangan.png';

const Pengembangan = () => {

  return (
    <div className="staydevelope-page">
      <div className="info" style={{
          width: 300,
          margin: "0 auto",
        }}>
        <img src={illustration} alt="" style={{
            width: "100%",
            height: "100%",
          }}/>
        <h1 className="hp medium" style={{ color: "#013237", textAlign: "center", }}>
          Oopps! Halaman ini masih dalam pengembangan..
        </h1>
      </div>
    </div>
  )

}

export default Pengembangan;
