import HomeIcon from '../components/icons/HomeIcon';
import PortfolioIcon from '../components/icons/PortfolioIcon';
import ForumIcon from '../components/icons/ForumIcon';
import SettingsIcon from '../components/icons/SettingsIcon';

import { Link, useLocation } from 'react-router-dom';
import { useState, useEffect } from 'react';

const Navbar = ({data}) => {

  const location = useLocation();
  const [click, setClick] = useState(false);

  function clicked(a) {
    setClick(!click);
  }

  const [icons, setIcons] = useState([
    [<HomeIcon click={click} location={location} />, "Beranda", "/"],
    [<PortfolioIcon click={click} location={location} />, "Portfolio", "/portfolio"],
    [<ForumIcon click={click} location={location} />, "Forum", "/forum"],
    [<SettingsIcon click={click} location={location} />, "Pengaturan", "/pengaturan"]
  ]);

  useEffect(() => {
    setIcons([
      [<HomeIcon click={click} location={location} path={icons[0][2]} />, "Beranda", "/"],
      [<PortfolioIcon click={click} location={location} path={icons[1][2]} />, "Portfolio", "/portfolio"],
      [<ForumIcon click={click} location={location} path={icons[2][2]} />, "Forum", "/forum"],
      [<SettingsIcon click={click} location={location} path={icons[3][2]} />, "Pengaturan", "/pengaturan"]
    ]);
  }, [click])

  return (
    <div className="navbar medium flex justify-between"
      style={{
        padding: "8px 16px",
        fontSize: 12,
        borderTop: "1px solid #9EAE9B",
        backgroundColor: "#FFF",
        position: "fixed",
        left: 0,
        right: 0,
        bottom: 0,
        zIndex: "10000",
        visibility: location.pathname === icons[0][2] ||
          location.pathname === icons[1][2] ||
          location.pathname === icons[2][2] ||
          location.pathname === icons[3][2] ? "visible" : "hidden",
      }}>
      {
        icons.map(icon => {
          return <Link to={icon[2]} style={{ textDecoration: "none", }} key={icon[1]}>
            <div onClick={clicked} className="icon flex flex-col items-center"
              style={{
                width: 72,
                color: "#C9D6C7",
              }}>
              {icon[0]}
              <div className={location.pathname == icon[2] ? "nav-color-active" : "nav-color"}>{icon[1]}</div>
            </div>
          </Link>
        })
      }

    </div>
  )

}

Navbar.defaultProps = {
  data: 1,
}

export default Navbar;
