import image from '../IMG/img-5.jpg';
import Bar from './Bar';
import { useState } from 'react';

const CardRecomendation = ({title, img, target, fund, directPath, currentID, data}) => {

  const [percent, setPercent] = useState(target/fund);
  // masalahnya di sini, jangan set state lagi (Error: Infinite Loop)
  // setPercent(100/percent);

  return (
    <div className="card"
      onClick={() => {
        directPath(currentID)
        // console.log(data);
      }}
      style={{
        width: 183,
        marginRight: 20,
      }}>
      <div className="imgPadding" style={{
          padding: 6,
          paddingBottom: 0,
        }}>
        <div className="imgWraper" style={{
            width: 171,
            height: 102,
            borderRadius: 12,
            overflow: "hidden",
          }}>
          <img src={img} alt=""style={{
              width: "100%",
              height: "100%",
              pointerEvents: "none",
            }}/>
          </div>
      </div>
      <div className="contentWraper"
        style={{
          padding: "14px 12px",
        }}>
        <div className="md medium" style={{color: "#013237"}}>{title}
        </div>
        <div className="flex justify-between" style={{marginTop: 4, marginBottom: 8,}}>
          <div className="sm regular secondary-gray" style={{color: "#A19CB9"}}>Terkumpul
          </div>
          <div className="sm regular text-gradient">{Math.floor(100/percent)}%
          </div>
        </div>
        <Bar percent={Math.floor(100/percent)} />
      </div>
    </div>
  )

}

CardRecomendation.defaultProps = {
  title: "Rumah di Perumahan Anggrek Purimas",
  img: image,
  target: 200000000,
  fund: 164000000,
}

export default CardRecomendation;
