import chartPieIcon from '../icons/chart-pie.svg';
import calculatorIcon from '../icons/calculator.svg';

const DetailProyekButton = ({text, type, detail}) => {

  return (
    <div className="border bg-gradient-green medium" style={{
        position: "relative",
        display: "grid",
        padding: 1,
        borderRadius: 12,
        fontSize: 16,
        marginBottom: 20,
      }}>
      <div className="csButton flex"
        style={{
          padding: 13,
          borderRadius: 12,
          background: type ? "none" : "#FFF",
        }}>
        <img src={detail ? chartPieIcon : calculatorIcon } alt="" style={{ marginLeft: 11, marginRight: 16, }}/>
        <div className={type ? "" : "text-gradient"} style={{
            color: type ? "#FFF" : "",
          }}>{detail ? text : "Kalkulator Investasi"}</div>
      </div>
    </div>
  )

}

DetailProyekButton.defaultProps = {
  text: "Detail Proyek",
  type: false,
  detail: true,
}

export default DetailProyekButton;
