import image from '../IMG/img-5.jpg';
import userIcon from '../icons/user.svg';
import calendarIcon from '../icons/calendar.svg';
import Button from '../components/Button';
import Bar from '../components/Bar';
import DetailProyekButton from '../components/DetailProyekButton';
import Maps from '../components/Maps';
import numberWithCommas from '../utilities/functions';
import { motion } from 'framer-motion';
import { useState } from 'react';

const Invest = ({title, img, target, fund, description, location, investor, duration}) => {

  const [percent, setPercent] = useState(target/fund);

  return (
    <div className="invest-page">
      <div className="carousel-image"
        style={{
          height: 260,
          width: "100%",
          position: "relative",
        }}>
        <div className="bg-layer-green"
          style={{
            width: "100%",
            height: "30%",
            position: "absolute",
            bottom: 0,
            left: 0,
          }}>
        </div>
        <div className="image-wraper" style={{ width: "100%", height: "100%", }}>
          <img src={img} alt="" style={{
              width: "100%",
              height: "100%",
              pointerEvents: "none",
            }}/>
        </div>
      </div>
      <div className="button-section bg-gradient-white" style={{
          position: "fixed",
          zIndex: 10,
          right: 0,
          left: 0,
          bottom: 0,
          padding: "40px 24px 30px",
          margin: "0 auto",
        }}>
        <Button />
      </div>
      <motion.div drag="y" dragConstraints={{top: -236, bottom: 0}}>

        <div className="detail" style={{
            padding: "0 24px",
            backgroundColor: "#FFF",
            borderRadius: "24px 24px 0 0",
            transform: "translateY(-24px)",
          }}>
          <div className="line-touch-padding" style={{
              padding: "12px 0 18px",
            }}>
            <div className="line-touch" style={{
                width: 30,
                height: 3,
                borderRadius: 30,
                margin: "0 auto",
                backgroundColor: "#C4C4C4",
              }}></div>
            </div>
            <h1 className="hp medium" style={{color: "#013237"}}>{title}
            </h1>
            <div className="icons-info flex justify-between" style={{
                padding: "24px 0 20px 0",
              }}>
              <div className="investors flex items-center">
                <img src={userIcon} alt="" style={{ marginRight: 8, }}/>
              <div className="sm medium" style={{ color: "#A19CB9" }}>
                {investor} Investor
              </div>
            </div>
            <div className="estimated-time flex items-center">
              <img src={calendarIcon} alt="" style={{ marginRight: 8, }}/>
            <div className="sm medium" style={{ color: "#A19CB9" }}>
              {duration} Bulan
            </div>
          </div>
        </div>
        <div className="fund flex justify-between">
          <h2 className="hs medium text-gradient">
            Rp {numberWithCommas(fund)}
          </h2>
          <h2 className="hs medium text-gradient">
            {Math.floor(100/percent)}%
          </h2>
        </div>
        <div className="bar-section" style={{ padding: "8px 0 16px" }}>
          <Bar percent={Math.floor(100/percent)} />
      </div>
      <div className="target flex">
        <h3 className="sm regular" style={{ marginRight: 8, }}>
          Target
        </h3>
        <h3 className="sm medium text-gradient">
          Rp {numberWithCommas(target)}
        </h3>
      </div>
      <div className="divider-section" style={{ padding: "24px 0" }}>
        <div style={{ width: "100%", height: 1, backgroundColor: "#C4C4C4", }}></div>
      </div>
      <div className="lg semi-bold" style={{ marginBottom: 20, color: "#013237" }}>Deskripsi Proyek
      </div>
      <p className="sm regular" style={{ color: "#A19CB9", }}>
        {description}
      </p>
      <div className="lg semi-bold" style={{ margin: "24px 0 20px", color: "#013237" }}>Lokasi
      </div>

      <Maps link={location} />

      <DetailProyekButton />
      <DetailProyekButton detail={false} />

      <div className="footer" style={{
          paddingBottom: 100,
        }}>
      </div>
    </div>
      </motion.div>
    </div>
  )

}

export default Invest;
